import React from 'react';
import { Button } from '@mui/material';
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import PropTypes from 'prop-types';
import LinearProgress from '@mui/material/LinearProgress';
import Paper from '@mui/material/Paper';


import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import { Grid } from '@mui/material';
import Typography from '@mui/material/Typography';

import { formatEthFromWei } from '../Formatters';
import { TeamInfoResponse, UserInfoResponse, _teamResponse } from '../assets/abi/farm-contract';
import { EventData } from 'ethereum-abi-types-generator';
//import { GetSiteInfoResponse } from '../assets/abi/farm-contract';
import SyncIcon from '@mui/icons-material/Sync';

interface TopPoolPanelProps {
  style?: React.CSSProperties;
  className?: string;
  lastDrawn: string;
}

const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
      
    </Box>
  );

  function TabPanel(props: any) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box sx={{ p: 0 }}>
            <div>{children}</div>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
  };
  
  const promisify = (inner: Function) =>
	new Promise((resolve, reject) =>
		inner((res: any, err: any) => {
			if (err) {
				reject(err)
			} else {
				resolve(res);
			}
		})
);

class TopPoolPanel extends React.Component<TopPoolPanelProps> {
  className?: string;
  style?: React.CSSProperties;
  lastDrawn: string;


  state = {
    showList: false,
    addresses: [],
    nicknames: [],
    deposits: [],
    tabValue: 0,
    showLoading: false,
    lastDrawnDate: "-",
    nextDrawnDate: "-",
    poolDeposits: []
  }
  baseState: any;
  _stateInterval: any;
  initialLoadDataDone: boolean = false;

  constructor(props: TopPoolPanelProps) {
      super(props);

      this.baseState = this.state;

      // properties
      this.className = props.className;
      this.style = props.style;
      this.lastDrawn = props.lastDrawn;

  }

  componentDidMount = () => { 

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval);

    this.getNextTopReferrers();

  }

  componentWillReceiveProps(props: TopPoolPanelProps) { 
    //console.log("componentWillReceiveProps", props);
    this.className = props.className;
    this.style = props.style;
    this.lastDrawn = props.lastDrawn;

    if(this.lastDrawn != "" && this.initialLoadDataDone == false){
      this.initialLoadDataDone = true;
      this.getNextTopReferrers();
    }
  }

  componentWillUnmount = () => { 


  }


 

  checkState = () => {  
    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }    

    if(this.lastDrawn != ""){
      let topSponsorRewardPoolLastDraw = new Date((parseInt(this.lastDrawn)) *1000  );
      let topSponsorRewardPoolCountdown = new Date((parseInt(this.lastDrawn)+86400) *1000  );

      this.setState({
        lastDrawnDate: topSponsorRewardPoolLastDraw.toLocaleString(),
        nextDrawnDate: topSponsorRewardPoolCountdown.toLocaleString()
      });
    }    
  }  

  getLastTopReferrers = () => {
    let topReferrals: string[] = [];
    let topReferralDeposits: string[] = [];

    this.setState({
      showLoading: true,
      showList: false
    });

    window.web3.eth.getBlockNumber().then( async currentBlock => {

      let step = 4000;

      for(let offset = step; offset < step * 15; offset += step){ // 15 retry attempts with 2k blocksteps backwards
        
        let result: EventData[] = await window.farm_contract.getPastEvents("PoolPayout", {                               
            fromBlock: currentBlock - offset,     
            toBlock: currentBlock - offset + step  
        });
        
        //console.log("fromBlock", currentBlock - offset, "toBlock", currentBlock - offset + step, result  );

        for(var i = result.length; i > result.length - 5; i--){
          let eventItem = result[i-1];
          if(typeof(eventItem) != "undefined"){
            topReferrals.push(eventItem.returnValues[0] as string);
            topReferralDeposits.push(eventItem.returnValues[1] as string);
          }
        }
        
        if(topReferrals.length >= 4){
         
          // data was found
          this.setState({
            poolDeposits: [],
            addresses: topReferrals.reverse(),
            deposits: topReferralDeposits.reverse(),
            showList: true
          });

          this.getAllNicknames();

          let currentCycle = await window.farm_contract.methods.pool_cycle().call();
          let previousCycle = parseInt(currentCycle) - 1;
          this.getPoolDepositsForCycle(previousCycle)
          //console.log("currentCycle", currentCycle);


          this.setState({showLoading: false});

          break; // stop searching 
        }
      }

    });

  }

  getAllNicknames = () => {

    this.setState({
      nicknames: [] // empty
    });

    let promises = [];		
    for(let i = 0; i < this.state.addresses.length; i++){			
      promises.push(promisify((callback: any) => window.farm_contract.methods.getNicknameToAddress(this.state.addresses[i]).call().then(callback)));			
    }

    Promise.all(promises).then((results) => {
      this.setState({
        nicknames: results
      });
    });
  
  }
  
  getPoolDepositsForCycle = (cycle: number) => {

    this.setState({
      poolDeposits: [] // empty
    });

    let promises = [];		
    for(let i = 0; i < this.state.addresses.length; i++){			
      promises.push(promisify((callback: any) => window.farm_contract.methods.pool_users_refs_deposits_sum(cycle.toString(), this.state.addresses[i]).call().then(callback)));			
    }

    Promise.all(promises).then((results) => {
      this.setState({
        poolDeposits: results
      });
    });
  
  }

  getNextTopReferrers = () => {
    if(!window.connected){
      return;
    }

    this.setState({
      showList: false
    });

    window.farm_contract.methods.poolTopInfo().call().then( result => {
      let topReferrals: string[] = [];
      let topReferralDeposits: string[] = [];
      for(var i = 0; i < result.addrs.length; i++){
        
        topReferrals.push(result.addrs[i] as string);
        topReferralDeposits.push(result.deps[i] as string);
      }

      this.setState({
        poolDeposits: [],
        addresses: topReferrals,
        deposits: topReferralDeposits,
        showList: true
      });

      this.getAllNicknames();
    });
  }

  tabHandleChange = (event: any, newValue: any) => {
    this.setState({tabValue: newValue});

    if(newValue === 0){
      this.getNextTopReferrers();
    }else{
      this.getLastTopReferrers();
    }
  }


  render = () => {

    let memberList;
    if (this.state.showList) { 
        memberList = this.state.addresses.map((walletAddress, index)=>{
        let nick = this.state.nicknames[index] as string;

        let phrase = " won ";
        let poolDeposit = "";

        if(this.state.poolDeposits.length > 0){
          poolDeposit = " for " + formatEthFromWei(this.state.poolDeposits[index]) + " BNB downline";
        }

        if(this.state.tabValue == 0){
          phrase = " has ";
        }


        let payout = phrase + formatEthFromWei(this.state.deposits[index] as string) + " BNB" + ((this.state.tabValue === 0)?" referral investments":"");
        payout = payout + poolDeposit;
        return <ListItem key={walletAddress}>
          <ListItemAvatar> 
            <Avatar>
              <EmojiEventsIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText primaryTypographyProps={{className: "truncate-text"}}  primary={walletAddress} secondary={
            <React.Fragment>
              <Typography
                sx={{ display: 'inline' }}
                component="span"
                variant="body2"
                color="secondary"
              >
                {nick}
              </Typography>
              {payout}
            </React.Fragment>
          } />
        </ListItem>
      });    
    } else {
      memberList = <ListItem>
      <ListItemText primaryTypographyProps={{className: "truncate-text"}}  primary="" secondary="" />
    </ListItem>;  
    }

    return (
      <Paper className={this.className} style={this.style}>

  <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
       <Tabs value={this.state.tabValue} onChange={this.tabHandleChange} aria-label="basic tabs example">
         <Tab label="Next Top Referrers" />
         <Tab label="Last Top Referrers" />
       </Tabs>
     </Box>
     <TabPanel value={this.state.tabValue} index={0}>
        <Grid spacing={1} container>
              <Grid item xs={9} md={9} sx={{p: 0}}>
                <Typography sx={{fontSize: 14, marginLeft: "16px", paddingTop: 2 }} color="secondary" gutterBottom>
                  Next Reward: {this.state.nextDrawnDate}
                </Typography>
              </Grid>
              <Grid item xs={3} md={3} >
                <div style={{paddingLeft: "0", paddingTop: "0", textAlign: "right"}}>
                  <Button fullWidth={false} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.getNextTopReferrers}><SyncIcon style={{height: "30px"}}></SyncIcon></Button>
                </div>             
              </Grid>
          </Grid>
            
          <div style={{display: (this.state.showLoading == true)?"block":"none"}}>
              <LinearProgress />
          </div>
          <List sx={{
            width: '100%',
            bgcolor: 'background.paper',
            position: 'relative',
            overflow: 'auto',
            maxHeight: "350px"
          }}>
          {memberList}
        </List>
     </TabPanel>


     <TabPanel value={this.state.tabValue} index={1}>

          <Grid spacing={1} container>
              <Grid item xs={9} md={9} sx={{p: 0}}>
                <Typography sx={{fontSize: 14, marginLeft: "16px", paddingTop: 2 }} color="secondary" gutterBottom>
                  Last Reward: {this.state.lastDrawnDate}
                </Typography>
              </Grid>
              <Grid item xs={3} md={3} >
                <div style={{paddingLeft: "0", paddingTop: "0", textAlign: "right"}}>
                  <Button fullWidth={false} variant={"outlined"} color={"secondary"} sx={{my: 1, marginRight: 1}} size="small" onClick={this.getLastTopReferrers}><SyncIcon style={{height: "30px"}}></SyncIcon></Button>
                </div>             
              </Grid>
          </Grid>
            
          <div style={{display: (this.state.showLoading == true)?"block":"none"}}>
              <LinearProgress />
          </div>
          <List sx={{
            width: '100%',
            bgcolor: 'background.paper',
            position: 'relative',
            overflow: 'auto',
            maxHeight: "350px"
          }}>
          {memberList}
        </List>
     </TabPanel> 

      </Paper>
    );
  }
}

export default TopPoolPanel;
