import React from 'react';
import { Button, StandardTextFieldProps, TextField } from '@mui/material';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import { Grid } from '@mui/material';
import Typography from '@mui/material/Typography';
import EditIcon from '@mui/icons-material/Edit';
import SettingsIcon from '@mui/icons-material/Settings';
import LinearProgress from '@mui/material/LinearProgress';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';

import { formatEthFromWei } from '../Formatters';
import { UserInfo2Response, _teamResponse } from '../assets/abi/farm-contract';


interface SettingsDlgProps {
  style?: React.CSSProperties;
  nickname?: string;
}

const modalStyle = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "70%",
  height: "60%",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
  color: "lightgrey",
  overflow: "auto",
  opacity: 1
};

const bull = (
    <Box
      component="span"
      sx={{ display: 'inline-block', mx: '2px', transform: 'scale(0.8)' }}
    >
      
    </Box>
  );

class SettingsDlg extends React.Component<SettingsDlgProps> {

  private nicknameInput: React.RefObject<StandardTextFieldProps>;

  state = {
    modalOpen: false,
    nickname: "",
    stopRefresh: false,
    showLoading: false,
  }
  baseState: any;
  _stateInterval: any;
  nickname?: string;
  style?: React.CSSProperties;

  constructor(props: SettingsDlgProps) {
      super(props);

      this.baseState = this.state;
      this.nicknameInput = React.createRef();

      // properties
      this.nickname = props.nickname;
      this.style = props.style;
  }

  componentDidMount = () => { 

    this.checkState();
    if(this._stateInterval){
      clearInterval(this._stateInterval);
    }
    this._stateInterval = setInterval(this.checkState, window.dataRefreshInterval + (window.dataRefreshInterval / 100 * 30 /*40% slower*/));
  }

  componentWillUnmount = () => { 


  }

  componentWillReceiveProps(props: SettingsDlgProps) { 
    this.nickname = props.nickname;
    this.style = props.style;

    if(this.state.stopRefresh == false){
      this.setState({nickname: this.nickname});
    }
  }


  onClick_SetNickname = () => {
    //this.setModalOpen();
    //window.farm_contract.methods.checkNickname(

    window.farm_contract.methods.checkNickname(this.state.nickname).call().then((result: boolean) => {
      if(result === true){
        this.setState({showLoading: true});
        window.farm_contract.methods.setUserNickname(this.state.nickname).send({from: window.walletAddress}).then(()=> {
          this.setState({showLoading: false});
        }).catch(err => {
          this.setState({showLoading: false});
        });
      }else{
        alert("Invalid Nickname");
      }
    });
  }

  onClick_RemoveNickname = () => {
      this.setState({showLoading: true});
      window.farm_contract.methods.removeUserNickname().send({from: window.walletAddress}).then(()=> {
        this.setState({showLoading: false});
      }).catch(err => {
        this.setState({showLoading: false});
      });
  } 

  checkState = () => {  
    if(window.connected !== true){
      this.setState(this.baseState);
      return;
    }    

  }  

  setModalOpen = () => {
    this.setState({modalOpen: true});
  }
  
  setModalClose = () => {
    this.setState({modalOpen: false, stopRefresh: false});
  }

  render = () => {

    let referralPart = (this.state.nickname)?this.state.nickname:window.walletAddress;
    let refAddress = <span>{window.location.origin + "?ref=" + referralPart}</span>;

    return (
    <div style={this.style}>
        <Button fullWidth={true} color={"secondary"} sx={{my: 1}} variant='text' size="small" onClick={this.setModalOpen}><SettingsIcon style={{height: "30px", marginRight: "8px"}}></SettingsIcon>settings</Button>
        <Modal
          open={this.state.modalOpen}
          onClose={this.setModalClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={modalStyle}>
            <Typography id="modal-modal-title" variant="h6" component="h2">
            Settings
            </Typography>

            <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Your Address:
            </Typography>
            <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" className={"truncate-text"} gutterBottom>
              {window.walletAddress}
            </Typography>

            <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
                Your Referal Link:
            </Typography>
            <Typography sx={{ fontSize: 14, marginBottom: 2 }} color="text.primary" className={"truncate-text"} gutterBottom>
              {refAddress}
            </Typography>

            <Typography sx={{ fontSize: 14, marginTop: "32px" }} color="text.secondary" gutterBottom>
               Set a nickname to hide your public address from referral links.
            </Typography>
            <Typography sx={{ fontSize: 10 }} color="text.secondary" gutterBottom>
               only characters a-z and numbers 0-9 are allowed here
            </Typography>
            <div>
              <TextField disabled={this.state.showLoading} inputProps={{maxLength: 10}} inputRef={this.nicknameInput} label={"Your Nickname"} value={this.state.nickname} onChange={(event) => this.setState({ nickname: event.target.value, stopRefresh: true })} type="text"></TextField>
            </div>
            <div style={{display: (this.state.showLoading == true)?"block":"none"}}>
              <LinearProgress />
            </div>

            <Grid container>
                <Grid item xs={12} md={12}>
                  <Button sx={{my: 1}} variant='outlined' size="small" onClick={this.onClick_SetNickname}><EditIcon style={{height: "30px", marginRight: "8px"}}></EditIcon>Change Nickname</Button>
                </Grid>
                <Grid item xs={12} md={12}>
                <Button sx={{my: 1}} disabled={this.state.nickname == ""} variant='outlined' size="small" onClick={this.onClick_RemoveNickname}><DeleteForeverIcon style={{height: "30px", marginRight: "8px"}}></DeleteForeverIcon>Remove Nickname</Button>
                </Grid>

              </Grid>
            
  
          </Box>
        </Modal>
    </div>
    );
  }
}

export default SettingsDlg;